import { getCourseLastBuy } from '@/api/course'
export default {
  props:{
    courseId:{
      type:Number,
      required:true,
    }
  },
  data() {
    return {
      data:{},
      timer:null,
      isUpdate:true
    }
  },
  mounted() {
    this.getLastBuyData()
    //30s拿一次数据
    this.timer = setInterval(() => {
      this.getLastBuyData()
    }, 30000)
  },
  beforeDestroy() {
    clearInterval(this.timer)
  },
  methods:{
    async getLastBuyData() {
      const {data:{code, data}} = await getCourseLastBuy(this.courseId)
      if (code === 0) {
        this.isUpdate = this.data.nickName && this.data.nickName !== data.nickName
        this.data = data
        //若无信息更新，5s后消失提示
        if(this.isUpdate) {
          setTimeout(() =>{
            this.isUpdate = false
          }, 5000)
        }
      }
    }
  }
}
