import { getCourseDetail, getCourseMsg } from "@/api/course"
import VideoDetail from '@/components/CourseDetail/VideoDetail'
export default {
  components: {
    VideoDetail
  },
  props:{
    isVideoWeChat:{
      type:Boolean,
      default:false
    }
  },
  data(){
    return{
      data: {},
    }
  },
  mounted () {
    this.getHandleData()
  },
  methods:{
    async getHandleData () {
      const { data } = this.$route.query.courseId ? await getCourseDetail(`${this.$route.query.courseId}`) : await getCourseDetail(0, {liveId:`${this.$route.params.id}`})
      if (data.code === 0) {
        let livesObj = {}
        data.data.lives.forEach(item => {
          if (!livesObj[item.chapterName]) {
            livesObj[item.chapterName] = []
            livesObj[item.chapterName].push(item)
          } else {
            livesObj[item.chapterName].push(item)
          }
        })
        data.data.livesObj = livesObj
        const {data:{code, data:courseObj}} = await getCourseMsg(data.data.courseId)
        if (code === 0) {
          this.data = {...data.data, ...courseObj}
        }
      }
    }
  },
  head() {
    return {
      title: this.data.name || '课程详情',
      meta: [
        {hid: 'description', name: 'description', content: this.data.name || '课程详情'}
      ]
    }
  }
}
