import { getCourseLiveDetail, reportCourseLived, getLoadPushMsg } from "@/api/course"
import { dynamicScript, dynamicCss, isWeChat, storage } from "ais-common-utils"
import config from "@/config/common.config"
import CommonContent from '@/components/CourseDetail/CommonContent'
import CommentPop from '@/components/Common/CommentPop.vue'
import CourseEvaluate from '@/components/CourseDetail/CourseEvaluate'
import VideoRecommend from '@/components/CourseDetail/VideoRecommend'
import CourseLastBuy from '@/components/CourseDetail/CourseLastBuy'
import WeChatShare from "@/mixins/WeChatShare"
import CourseClock from '@/components/CourseDetail/CourseClock'
import Vue from 'vue'
export default {
  components: {
    CommonContent,
    CommentPop,
    CourseEvaluate,
    VideoRecommend,
    CourseLastBuy,
    CourseClock
  },
  mixins: [WeChatShare],
  props:{
    commonData:{
      type:Object,
      required:true
    },
    isVideoWeChat:{
      type:Boolean,
      required:true
    }
  },
  data(){
    return {
      data:{},
      scene:0,
      watchedTime:0, //记录观看时长
      isChange:true, //屏幕切换
      liveInfoTimer:null, //直播播放定时器
      liveId:null, //视频id
      isPreviewEnd: false, //判断试听结束
      isEnd:false,  //视频播放结束，暂时隐藏video,显示弹窗
      isLiveEnd:false, 
      initEvaluate:false, //评价显示
      loadMsg:{}, //推荐信息
      isWeChat:false, //是否微信环境
      shareOption:{}, //微信分享配置
      show:false, //微信提示图片显示
      liveTimeInterval:2000, //直播获取观看人数接口请求间隔
    }
  },
  watch:{
    $route(){
      this.scene = 0
      this.player = null
      this.chatRoom = null
      this.liveSdk = null
      clearTimeout(this.timer)
      clearTimeout(this.liveTimer)
      clearTimeout(this.liveInfoTimer)
      this.getLiveConfig()
    }
  },
  mounted(){
    this.isWeChat = isWeChat()
    this.getLiveConfig()
    document.getElementsByClassName('layout')[0].setAttribute('style', 'min-height:100% !important;')
  },
  beforeDestroy(){
    if(this.liveSdk){
      this.liveSdk.destroy(true)
    }
    if(this.player){
      this.player.dispose()
    }
    this.player = null
    this.chatRoom = null
    this.liveSdk = null
    clearTimeout(this.timer)
    clearTimeout(this.liveTimer)
    clearTimeout(this.liveInfoTimer)
    document.getElementsByClassName('layout')[0].removeAttribute('style')
  },
  methods:{
    async getLiveConfig(){
      this.liveId = this.$route.query.liveId||this.$route.params.id
      let liveParams = {}
      //未购买，且有试听的，进来走试听接口;
      const liveItem = this.commonData.lives.find(p => p.id === +this.liveId)
      let itemPreviewTime = !this.commonData.apply ? liveItem.previewTime : 0
      //试听，带preview：1参数
      if(this.$route.query.isPreview || itemPreviewTime) {
        liveParams.preview = 1
      }
      //本地有保存邀请人信息的，要把邀请人信息带上
      const inviterObj = storage.get('inviterObj')
      if(inviterObj && ((+inviterObj.courseId) === this.commonData.courseId)) {
        liveParams.inviterCode = inviterObj.inviterCode
        storage.remove('inviterObj')
      }
      const { data } = await getCourseLiveDetail(`${this.commonData.courseId}/${this.liveId}`, liveParams)
      if(data.code === 0){
        this.scene = data.data.scene
        //获取推荐信息
        if (data.data.hasOwnProperty('push')) {
          const {data:{code, data:loadMsg}} = await getLoadPushMsg({courseId:this.commonData.courseId, liveId:this.liveId})
          if (code === 0){
            this.loadMsg = loadMsg
          }
        }
        const isSharePush = this.loadMsg.inviterCode && this.loadMsg.pushCoupon && (this.loadMsg.pushCoupon.drawEnd > parseInt((new Date()).getTime()/1000))
        //设置微信第三方分享参数
        this.shareOption = {
          title: this.commonData.name,
          desc: `我已参加这个课程，你也来一起参加吧!`,
          imgUrl: `${config.IMG_URL.default}${this.commonData.poster}`,
          link: isSharePush ? `${config.LINK.school_h5}/courseDetail/${this.commonData.courseId}?inviterCode=${this.loadMsg.inviterCode}`:`${config.LINK.school_h5}/courseDetail/${this.commonData.courseId}`
          //link: isSharePush ? `http://t3.ais.cn/courseDetail/${this.commonData.courseId}?inviterCode=${this.loadMsg.inviterCode}` : `http://t3.ais.cn/courseDetail/${this.commonData.courseId}`
        }
        //设置微信分享SDK
        if(this.isWeChat || this.isVideoWeChat){
          this.getWechatShare(this.commonData.courseId)
        }
        //播放设置
        this.$nextTick( async () =>{
          this.data = data.data
          if (!window.PolyvLiveSdk && !window.PolyvChatRoom && this.data.scene !== 1 ) { //直播
            await dynamicScript(config.liveUrl, 'PolyvLiveSdk')
            await dynamicScript(config.chatUrl, 'PolyvChatRoom')
            dynamicCss(config.chatCss)
          } else if (!window.Aliplayer && this.data.videoId && this.data.scene === 1) { //录播
            await dynamicScript(config.recordUrl, 'Aliplayer')
            dynamicCss(config.recordCSS)
          }
          //构造录播播放器
          if(data.data.scene ===1){
            this.player = new window.Aliplayer({
              //x5_type:'h5',
              id: 'players',
              width: '100%',
              height: '205px',
              autoplay: false,
              vid: this.data.videoId,
              playauth: this.data.playAuth,
              playConfig:{
                MtsHlsUriToken:this.data.hlsUriToken
              },
              useH5Prism:true,
              playsinline:true,
              encryptType:1,
              preload:false, //取消自动加载
              hlsUriToken:this.data.hlsUriToken, //随机效验签名
              isPreview: this.$route.query.isPreview || itemPreviewTime  //是否试看
            }, (player) => {
              //非试看，有观看历史设置续播时间，切观看历史长度小于视频长度
              if(this.data.watched && this.data.watched< this.data.duration && !(this.$route.query.isPreview && itemPreviewTime)) {
                player.seek(this.data.watched)
              }
              //设置试看时间
              if(this.$route.query.isPreview && this.data.previewTime > 0) {
                player.setPreviewTime(this.data.previewTime*60)
              } else if (itemPreviewTime > 0){
                player.setPreviewTime(itemPreviewTime*60)
              }
            })
            //监听播放
            this.player.on('playing', () => {
              this.isEnd = false
              this.report()
            })
            //监听暂停
            this.player.on('pause', () => {
              clearTimeout(this.timer)
            })
            //监听结束
            this.player.on('ended', async () => {
              //判断试听结束
              if(this.$route.query.isPreview || itemPreviewTime > 0) {
                this.isPreviewEnd = true
              }
              const params = { courseId:this.commonData.courseId, liveId:this.liveId, options: {duration: parseInt(this.player.getDuration()), watched: parseInt(this.player.getCurrentTime())}}
              await reportCourseLived(params)
              clearTimeout(this.timer)
              //观看结束，弹出评价窗口
              if(!this.commonData.evaluated && !this.$route.query.isPreview) {
                this.$refs.comment.toggle(true)
              }
              this.isEnd = true
            })
          } else {
            //聊天室
            let tabData = [{name:'课程介绍', type:'document'}, {name:'评论', type:'evaluate'}, {name:'打卡区', type:'clock'}]
            this.chatRoom = new window.PolyvChatRoom({
              roomId: Number(this.data.channelId),
              userId: this.data.chatUserId,
              nick: this.data.chatNickName ? this.data.chatNickName : '艾思用户',
              pic:this.data.chatAvatar ? `${this.data.chatAvatar}` : `https://img.ais.cn/thirds/xcx/images/user/avator.png`,
              userType: 'slice',
              token: this.data.chatToken[0],
              mediaChannelKey:this.data.chatToken[1],
              version: '2.0',
              container: '#wrap',
              width: '100%',
              height: `${document.body.clientHeight - 248 }px`,
              enableAsk:false,  //关闭提问
              tabData:this.scene === 3 ? [ {name:'课程文档', type:'ppt'}, ...tabData ] : tabData
            })
            let chat = this.chatRoom.chat
            //直播
            this.liveSdk = new window.PolyvLiveSdk({
              channelId: Number(this.data.channelId),
              sign: this.data.sign, // 频道验证签名
              timestamp: this.data.timestamp, // 毫秒级时间戳
              appId: this.data.appId, // polyv 后台的appId
              user: {
                userId: this.data.chatUserId,
                userName: this.data.chatNickName ? this.data.chatNickName : '艾思用户',
                pic: this.data.chatAvatar ? `${this.data.chatAvatar}` : `https://img.ais.cn/thirds/xcx/images/user/avator.png`
              },
              socket: chat.socket
            })
            const liveConfig = {
              pptEl: '#tab-ppt',
              el: '#player',
              type: 'auto',
              autoplay: true,
              audioMode: false,
              controller: true,
              pptNav: true,
              controllerPosition:'ppt',
              switchPlayer:true,
              pptNavBottom: '30px',
              controllerEl:document.getElementById('#player')
            }
            this.liveSdk.on(window.PolyvLiveSdk.EVENTS.CHANNEL_DATA_INIT, () => {
              this.liveSdk.setupPlayer({
                ...liveConfig
              })
              //开始直播监听
              this.liveSdk.player.on('playing', () => {
                this.getLiveInfo()
              })
              this.liveReport()
            })
            //插入课程详情
            document.getElementById('tab-document').innerHTML = this.commonData.content
            //插入评论
            document.getElementById('tab-evaluate').appendChild(document.getElementById('CourseEvaluate'))
            //插入打卡区
            document.getElementById('tab-clock').appendChild(document.getElementById('CourseClock'))
            this.initEvaluate = true
            this.$refs.CourseEvaluate.getCommentList()
            //this.$refs.CourseClock.getClockRecordList()
          }
        })
      }
    },
    // 点击登录
    goToAis() {
      const url = encodeURIComponent(window.location.origin + this.$route.fullPath)
      window.location.href = `${config.POST.school_h5}/school/passport/login?mobile=1&state=${url}`
    },
    // 录播上报
    report () {
      this.timer = setTimeout(async () => {
        if(this.player) {
          const duration= parseInt(this.player.getDuration())
          const watched = parseInt(this.player.getCurrentTime())
          if(watched - this.watchedTime<9){
            return
          }
          const params = { courseId:this.commonData.courseId, liveId:this.liveId, options: {duration: duration, watched: watched}}
          const { data } = await reportCourseLived(params)
          if (data.code === 0) {
            this.watchedTime = watched
            this.report()
          }
        }
      }, 10000)
    },
    //直播上报
    liveReport (){
      this.liveTimer = setTimeout(async () => {
        const watched = parseInt(this.liveSdk.player.currentTime)
        if(watched!== 0 && (watched - this.watchedTime<9)){
          return
        }
        const params = { courseId:this.commonData.courseId, liveId:this.liveId, options: {duration: 0, watched: watched}}
        const { data } = await reportCourseLived(params)
        if(data.code === 0){
          this.watchedTime = watched
          this.liveReport()
        }
      }, 10000)
    },
    //直播信息获取
    getLiveInfo () {
      clearTimeout(this.liveInfoTimer)
      this.liveInfoTimer = setTimeout( () => {
        Vue.jsonp(`${config.POST.school_h5}/school/course/info/live/${this.liveId}`).then((res) => {
          this.$set(this.data, 'visits', res.visits)
          //直播结束,弹出评价窗口
          if (res.liveStatus === 2 && !this.isEnd && !this.commonData.evaluated && !this.isLiveEnd) {
            this.$refs.comment.toggle(true)
            this.isEnd = true
            this.isLiveEnd = true
          } 
          //直播中，接口请求间隔改为5s一次;
          this.liveTimeInterval = res.liveStatus === 1 ? 5000 :2000
          this.getLiveInfo()
        })
      }, this.liveTimeInterval)
    },
    //试看重播
    previewLoad() {
      this.isPreviewEnd = false
      this.player.seek(0)
      this.player.replay()
    },
    //购买课程
    buyCourse() {
      this.$router.push({
        path:`/courseOrder/${this.commonData.courseId}`
      })
    },
    //课程评论后刷新
    updateComment() {
      this.$emit('update')
      this.isEnd = false
    },
    //课程评论弹窗隐藏video
    setIsEnd(bool) {
      this.isEnd = bool
    }
  },
}
